.wrapper {
  width: 80%;
  margin: 0 auto;
  padding-bottom: 2em;
}

.title {
  font-family: "ProximaSoft";
  margin: 0;
  padding: 1em 0;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.imgWrapper {
  width: 30%;
}

.foto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 3.5em;
  margin-left: 10%;
  color: white;
  font-weight: bold;
  background-color: rgb(255, 218, 0);
  font-size: 2em;
  border-radius: 20px;
  text-align: center;
  border: none;
  outline: none;
  cursor: pointer;
  box-shadow: 1px 1px 5px #888888;
  padding: 5px;
}

.foto:hover,
.foto:focus {
  background-color: rgb(255, 212, 0);
  outline: none;
}

.foto:active {
  background-color: rgb(255, 212, 0);
  outline: none;
  margin-top: 2px;
}

@media (min-width: 480px) {
  .wrapper {
    font-size: 1.2em;
  }
}

@media (min-width: 768px) {
  .wrapper {
    font-size: 1.5em;
    padding-bottom: 3em;
  }
  .title {
    padding-top: 2em;
  }
}

@media (min-width: 960px) {
  .wrapper {
    width: 70%;
    font-size: 1.8em;
  }
}

@media (min-width: 1200px) {
  .wrapper {
    width: 60%;
    font-size: 2em;
  }
}

@media (min-width: 1440px) {
  .wrapper {
    font-size: 2.5em;
  }
}
