.wrapper {
  margin: 0 auto 2em;
  padding-top: 120px;
  width: 80%;
}

.groupList {
  margin: 2em 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2vw;
}

.groupItem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 2.2em;
  border-radius: 20px;
  font-size: 1.3em;
  font-weight: bold;
  box-shadow: 1px 1px 5px #888888;
  background: rgb(173, 28, 226);
  color: #ffffff;
  cursor: pointer;
}
.list {
  margin: 2em 0;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  grid-gap: 2vw;
}

.card {
  display: flex;
  padding: 10px;
  border-radius: 2px;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.groupImgWrapper {
  margin: 3em auto 0;
  /* width: 50%; */
}

.imgWrapper {
  width: 50%;
  margin-right: 5%;
}

.description {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-size: 1.1em;
}
.name {
  display: flex;
  flex-direction: column;
  width: 65%;
  margin-right: 5%;
}
.descItem {
  padding-bottom: 0.5em;
}

.price {
  width: 30%;
  padding-bottom: 0;
}

.descItemResult {
  font-weight: bold;
}

@media (min-width: 480px) {
  .list {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 960px) {
  .list {
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
